
import AvatarBadge from '../UI/Auth/AvatarBadge.vue'
import {defineComponent} from "vue"
export default defineComponent({
    name: 'AppSidebar',
    components: { AvatarBadge },
    data: () => ({
        links: [
            {   
                name: 'main',
                label: 'Контакты',
                icon: 'fa-solid fa-book',
                exact: true
            },
            {   
                name: 'saved',
                label: 'Сохранённые сообщения',
                icon: 'fa-solid fa-save',
                callback: 'onSaveTap'
            },
            {   
                name: 'settings',
                label: 'Пользователь',
                icon: 'fa-solid fa-user',
                callback: 'profileEdit'
            },
            {   
                name: 'settings',
                label: 'Настройки',
                icon: 'fa-solid fa-gear',
                callback: 'configure'
            }
        ],
        footerBtns: [
            {   
                name: 'logout',
                label: 'Выход',
                icon: 'fa-solid fa-arrow-right-from-bracket',
                callback: 'logout',
                exact: false
            }
        ]
    }),
    methods: {
        onSaveTap(): void {
            this.$toast.show('Пока тут ничего нет')
        },
        async logout(): Promise<void> {
            await this.$store.dispatch('auth/logout')
            this.$router.push({name: "login", query: {
                message: "auth/session-closed"
            }})
        },
        profileEdit(): void {
            this.$router.push({name: 'profile-edit', params: {uid: this.user.uid}})
        },
        configure(): void {
            this.$toast.show('Пока тут ничего нет')
        },
        onClick(link): void {
            if(!link.callback) {
                this.$router.push({ name: link.name, })
            }
            else if( this[link.callback] ) this[link.callback]()
        }
    },
    computed: {
        user() {
            return this.$store.getters['auth/getUser']
        }
    }
})
